import { Component, ViewChild } from '@angular/core';
import { ChildTwoComponent } from './child-two.component';
@Component({

	selector:'app-parent-two',
	template: `
	<button (click) ="onAddForChild();">Add for Child</button>
	<app-child-two ></app-child-two>
	`
})

export class ParentTwoComponent {

	@ViewChild(ChildTwoComponent)
	myChild:ChildTwoComponent;

	onAddForChild(){

		this.myChild.value ++;
	}

	
}